import React from 'react'

import Stars from '../Stars'

import { DoctorItemContainer, DoctorImage, DoctorInfo, DoctorName, DoctorType, DoctorStars, DoctorDescription, DoctorLink } from '../../styles/Doctors'

function DoctorItem({ doctor, onClick }) {

    const handleBtn = () => onClick(doctor.id) 

    return (
        <DoctorItemContainer onClick={handleBtn}>
            <DoctorImage src={doctor.imagen}/>
            <DoctorInfo>
                <DoctorName>{doctor.nombre}</DoctorName>
                <DoctorType>{doctor.cargo?.toUpperCase()}</DoctorType>
                <DoctorStars>
                    <Stars value={Math.floor(doctor.estrellas)}/>
                </DoctorStars>
                {doctor.descripcion?.length <= 100 ? (
                    <DoctorDescription>{doctor.descripcion}</DoctorDescription>
                ):(
                    <div>
                        <DoctorDescription>
                            {doctor.descripcion?.slice(0,85)}
                            <DoctorLink>VER MÁS...</DoctorLink>
                        </DoctorDescription>
                        
                    </div>
                )}
            </DoctorInfo>
            
        </DoctorItemContainer>
    )
}

export default DoctorItem
