import React, { useEffect, useState } from 'react'
import axios from 'axios'

import Navbar from '../components/Navbar'
import FormGroup from '../components/date/FormGroup'

import { MainContainer, ContentContainer, TitleContainer } from '../styles/Date'

import { URL } from '../variables'

function Date({ location }) {
    const [type, setType] = useState('')
    const [info, setInfo] = useState({})

    const getInfo = async (id, infoType) => {
        let res = await axios({
            url: `${URL}/${infoType}/${id}?reviews=false`,
            method: 'GET',
            timeout: 5000
        })
        return res.data
    }

    const checkQueries = () => {
        const query = new URLSearchParams(location.search);
        if(query.get('id') && query.get('type')){
            if(query.get('type') !== "medico" && query.get('type') !== "laboratorio"){
                window.location.replace('/cita')
            }
            return { ok: true, id: query.get('id'), type: query.get('type') }
        }
        return { ok: false }
    }

    useEffect(() => {
        const { ok, id, type } = checkQueries()
        if(ok){
            getInfo(id, type).then( ({ info }) => {
                console.log(info)
                setInfo(info)
                setType(type)
            }).catch( e => {
                if(e.response.status === 400){window.location.replace('/cita')}
            })
        }
        
    }, [])

    return (
        <MainContainer>
            <Navbar bgNavbar={true}/>
            <ContentContainer>
                <TitleContainer>
                    <h1>CITA</h1>
                </TitleContainer>
                <FormGroup 
                    dateItem={info} 
                    dateType={type}
                />
            </ContentContainer>
        </MainContainer>
    )
}

export default Date
