import React, { useState } from 'react'

import { ReactComponent as UserIcon } from '../../assets/icons/usuario.svg';

import { LoginFormContainer, LoginForm, LoginTitle, LoginFormLabel, LoginInput, RememberPasswordContainer, DiscountAnnouncement, DiscounntTextBold, LineSeparator, 
        LoginFormLabelCheckbox, LoginFormCheckbox, LoginFormBtn, LoginFormForgotPasswordContainer, LoginFormForgotPassword, DiscounntText } from '../../styles/Home'

function Login() {

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [rememberPassword, setRememberPassword] = useState(false)

    const handleUsername = e => { setUsername(e.target.value)}
    const handlePassword = e => setPassword(e.target.value)
    const handleCheckbox = () => setRememberPassword(!rememberPassword)

    return (
        <LoginFormContainer>
            <DiscountAnnouncement>
                <DiscounntTextBold color="rgb(178,223,252)" size="4vw">15% DESCUENTO</DiscounntTextBold>
                <DiscounntText>EN CUALQUIER SERVICO</DiscounntText>
                <DiscounntTextBold size="5vw">PAGANDO AQUÍ</DiscounntTextBold>
                <LineSeparator
                    color="rgb(178,223,252)"
                    height="0.7vw"
                />
            </DiscountAnnouncement>
            <LoginForm>
                <UserIcon />
                <LoginTitle>INICIO DE SESIÓN</LoginTitle>
                <LoginFormLabel>USUARIO</LoginFormLabel>
                <LoginInput 
                    onChange={handleUsername}
                    value={username}
                />
                <LoginFormLabel>CONTRASEÑA</LoginFormLabel>
                <LoginInput 
                    onChange={handlePassword}
                    value={password}
                    type="password"
                />
                <RememberPasswordContainer>
                    <LoginFormLabelCheckbox>Recordar contraseña</LoginFormLabelCheckbox>
                    <LoginFormCheckbox 
                        type="checkbox"
                        value={rememberPassword}
                        onChange={handleCheckbox}
                    />
                </RememberPasswordContainer>
                <LoginFormBtn>ACEPTAR</LoginFormBtn>
                <LoginFormForgotPasswordContainer>
                    <LoginFormForgotPassword href="">Recuperar contraseña</LoginFormForgotPassword>
                    <LoginFormForgotPassword href="">Crear nueva cuenta</LoginFormForgotPassword>
                </LoginFormForgotPasswordContainer>
            </LoginForm>
            
        </LoginFormContainer>
    )
}

export default Login
