import React, { useState } from 'react'

import { FormContainer, InputContainer, InputLabel, Input, TextArea } from '../../styles/Date'

function Form() {

    const [name, setName] = useState('')
    const [age, setAge] = useState('')
    const [illness, setIllness] = useState('')
    const [phone, setPhone] = useState('')
    const [email, setEmail] = useState('')

    const handleName = e => setName(e.target.value)
    const handleAge = e => setAge(e.target.value)
    const handleIllness = e => setIllness(e.target.value)
    const handlePhone = e => setPhone(e.target.value)
    const handleEmail = e => setEmail(e.target.value)

    return (
        <FormContainer>
            <InputContainer>
                <InputLabel htmlFor="nombre">Nombre:</InputLabel>
                <Input 
                    id="nombre"
                    value={name}
                    onChange={handleName}
                />
            </InputContainer>
            <InputContainer>
                <InputLabel htmlFor="edad">Edad:</InputLabel>
                <Input 
                    id="edad"
                    value={age}
                    onChange={handleAge}
                />
            </InputContainer>
            <InputContainer align="flex-start">
                <InputLabel htmlFor="padecimiento">Padecimiento:</InputLabel>
                <TextArea 
                    id="padecimiento"
                    value={illness}
                    onChange={handleIllness}
                />
            </InputContainer>
            <InputContainer>
                <InputLabel htmlFor="telefono">Teléfono:</InputLabel>
                <Input 
                    id="telefono"
                    value={phone}
                    onChange={handlePhone}
                />
            </InputContainer>
            <InputContainer>
                <InputLabel htmlFor="email">Email:</InputLabel>
                <Input 
                    id="email"
                    value={email}
                    onChange={handleEmail}
                />
            </InputContainer>
        </FormContainer>
    )
}

export default Form
