import React from 'react'

import Form from './Form'
import Stars from '../Stars'

import { GroupContainer, DateItemContainer, DateItemImage, DateItemName, DateItemText } from '../../styles/Date'

import { ReactComponent as HemIcon } from '../../assets/icons/hematologia.svg';
import { ReactComponent as CoaIcon } from '../../assets/icons/coagulacion.svg';
import { ReactComponent as QuSaIcon } from '../../assets/icons/quimica-sanguinea.svg';

function UserForm({ dateItem, translateX, dateType }) {

    const getImage = () => {
        if(dateType === "laboratorio"){
            switch(dateItem.laboratorio){
                case "HEMATOLOGÍA": return( <HemIcon className="icon-lab"/> )
                case "COAGULACIÓN": return( <CoaIcon className="icon-lab"/> )
                case "QUÍMICA SANGUINEA": return( <QuSaIcon className="icon-lab"/> )
                default:
                    break;
            }
        }
        return (<DateItemImage src={dateItem.imagen}/>)
    }

    return (
        <GroupContainer translateX={translateX}>
            <Form />
            <DateItemContainer>
                {getImage()}
                {dateType === "laboratorio" ? (
                    <>
                    <DateItemName>{dateItem.laboratorio}</DateItemName>
                    <DateItemText>{dateItem.nombre}</DateItemText>
                    </>
                ): (
                    <>
                    <DateItemName>{dateItem.nombre}</DateItemName>
                    <DateItemText>{dateItem.cargo}</DateItemText>
                    </>
                )}
                <Stars value={dateItem.estrellas}/>
            </DateItemContainer>
        </GroupContainer>
    )
}

export default UserForm
