import styled from 'styled-components'

//const purple = "rgb(88,32, 142)"
const darkBlue = "rgb(0,47,91)"
const lightGray = "rgb(235,235,235)"
//const lightBlue = "rgb(178,223,252)"

export const MainContainer = styled.div`
    width: 100%;
    height: 100%;
    overflow-x: hidden;
`

export const ContentContainer = styled.div`
    width: 100%;
    padding-top: 10vw;
    display: flex;
    align-items: center;
    flex-direction: column;
`

export const TitleContainer = styled.div`
    width: 76vw;
    height: 5vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 2vw;
    border-bottom: 0.2vw solid ${darkBlue};
    margin-bottom: 2vw;

    h1{
        font-family: Trajan;
        color: ${darkBlue};
        font-size: 2vw;
        margin: 0;
    }
`

export const FormGroupContainer = styled.div`
    width: 100vw;
    height: 60vh;
`
export const FormGroupItemsContainer = styled.div`
    width: 100vw;
    height: 60vh;
    display: flex;
    flex-direction: row;
`

export const GroupContainer = styled.div`
    width: 70vw;
    height: 57vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding:  0 15vw;
    flex-shrink: 0;
    transform: ${props => `translateX(${props.translateX}vw)`};
    transition: transform 500ms ease-in;
`

export const FormContainer = styled.div`
    width: 50vw;
`

export const InputContainer = styled.div`
    width: 50vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1vw;
`

export const InputLabel = styled.label`
    font-family: MontserratMedium;
    color: ${darkBlue};
    font-size: 1.5vw;
    width: 15vw;
`

export const Input = styled.input`
    background-color: ${lightGray};
    width: 35vw;
    border-radius: 0.3rem;
    padding: 0 1vw;
    height: 3vw;
    border: 0;
    font-family: Montserrat;
    font-size: 1vw;
    &:focus{ outline: 0 }
`

export const TextArea = styled.textarea`
    background-color: ${lightGray};
    width: 35vw;
    border-radius: 0.3rem;
    padding: 1vw;
    height: 5vw;
    border: 0;
    font-family: Montserrat;
    resize: none;
    font-size: 1vw;
    &:focus{ outline: 0 }
`

export const DateItemContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`

export const DateItemImage = styled.img`
    width: 12vw;
    height: 12vw;
    border-radius: 0.5rem;
    border: solid 1px ${darkBlue};
    margin-bottom: 1vw;
`

export const DateItemName = styled.h1`
    font-family: MontserratMedium;
    font-size: 1.2vw;
    margin: 0;
    margin-bottom: 0.3vw;
`

export const DateItemText = styled.h2`
    font-family: MontserratLight;
    font-size: 1.2vw;
    margin: 0;
    margin-bottom: 0.3vw;
`

export const BtnsCarrouselContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 80vw;
    margin: 0 10vw;
    position: relative;
    top: -30vh;
    z-index: 1;
`

export const BtnCarrousel = styled.button`
    background-color: ${darkBlue};
    width: 2vw;
    height: 2vw;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:0;
    border: 0;
    color: white;
    font-size: 0.7vw;
    &:hover{ cursor: pointer; }
`

export const ItemsContainer = styled.div`
    width: 70vw;
    height: 57vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
`