import React, { useState, useEffect } from 'react'
import axios from 'axios'

import Navbar from '../components/Navbar'
import DoctorsGroup from '../components/doctors/DoctorsGroup'

import { MainContainer, DoctorsContainer } from '../styles/Doctors'

import { URL } from '../variables'

function Doctors() {

    const [doctors, setDoctors] = useState([])
    const [nutriologist, setNutriologist] = useState([])

    const getDoctors = async () => {
        let res = await axios({
            method: 'GET',
            url: `${URL}/all-medicos`,
            timeout: 3000
        })
        return res.data
    }

    useEffect(() => {
        getDoctors().then( ({doctors}) => {
            let docs = doctors.filter( doc => doc.tipo === "MEDICO" )
            let nutri = doctors.filter( doc => doc.tipo === "NUTRIOLOGO" )
            setDoctors(docs)
            setNutriologist(nutri)
        }).catch( (e) => {
            alert('Ha sucedido un error en el servidor. Intentalo más tarde.')
        })
    }, [])

    return (
        <MainContainer>
            <Navbar bgNavbar={true}/>
            <DoctorsContainer>
                <DoctorsGroup 
                    doctors={doctors}
                    title="MÉDICOS"
                />
                <DoctorsGroup 
                    doctors={nutriologist}
                    title="NUTRIÓLOGOS"
                    closed={true}
                />
            </DoctorsContainer>
        </MainContainer>
    )
}

export default Doctors
