import React, { useState } from 'react'

import UserForm from './UserForm'
import Calendar from './Calendar'
import ItemsGroup from './ItemsGroup'

import { FormGroupContainer, BtnsCarrouselContainer, BtnCarrousel, FormGroupItemsContainer } from '../../styles/Date'

function FormGroup({ dateItem, dateType }) {

    const [currentIdx, setCurrentIdx] = useState(0)

    const next = () => {
        if(JSON.stringify(dateItem) === "{}"){
            if(currentIdx !== 3){ setCurrentIdx(currentIdx +1 ) }
        }else{
            if(currentIdx !== 2){ setCurrentIdx(currentIdx +1 ) }
        }
    }

    const back = () => {
        if(currentIdx !== 0){ setCurrentIdx(currentIdx -1 ) }
    }

    return (
        <FormGroupContainer>
            <FormGroupItemsContainer>
                <Calendar translateX={currentIdx * -100}/>
                {JSON.stringify(dateItem) === "{}" && <ItemsGroup dateType={dateType} translateX={currentIdx * -100}/>}
                <UserForm 
                    dateItem={dateItem} 
                    translateX={currentIdx * -100}
                    dateType={dateType}
                />
            </FormGroupItemsContainer>
            <BtnsCarrouselContainer>
                <BtnCarrousel onClick={back}>{'<<<'}</BtnCarrousel>
                <BtnCarrousel onClick={next}>{'>>>'}</BtnCarrousel>
            </BtnsCarrouselContainer>
        </FormGroupContainer>
    )
}

export default FormGroup
